	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"No Rest for the Wicked","date":"16 Oct 2001","slug":"/2001-10-16-no-rest-for-the-wicked/","tags":"Y2001"},"html":"<p>Christopher has no mercy, he was up at 6 am again today.</p>\n<p>Mama's car is silver and looked like an airliner when she pulled into the driveway last night. And thanks to GPS, we know our house lies 50 m above sea level.</p>"},"next":{"fields":{"title":"Turbo Day","date":"15 Oct 2001","slug":"/2001-10-15-turbo-day/"}},"previous":{"fields":{"title":"GI Virus","date":"17 Oct 2001","slug":"/2001-10-17-gi-virus/"}}},{"node":{"fields":{"title":"Turbo Day","date":"15 Oct 2001","slug":"/2001-10-15-turbo-day/","tags":"Y2001"},"html":"<p>Mama's getting her new <a href=\"http://www.bmw.com/bmwe/products/automobiles/5er/sedan/index.html\">company car</a> (530i) today, 6 months after her promotion, and in the wrong color. I'm only slightly jealous. Then again, without me, she'll never be able to program the CD changer or the navigation system. To bring some speed into my own life, I bought 256 MB RAM (up from 64 MB) for my ThinkPad.</p>\n<p>Andrea <a href=\"http://andrea.editthispage.com/2001/10/15\">didn't receive any (postal) mail today</a> because of an anthrax scare at the Bonn post office.</p>"},"next":{"fields":{"title":"Bad Day at the Races","date":"14 Oct 2001","slug":"/2001-10-14-bad-day-at-the-races/"}},"previous":{"fields":{"title":"No Rest for the Wicked","date":"16 Oct 2001","slug":"/2001-10-16-no-rest-for-the-wicked/"}}},{"node":{"fields":{"title":"Bad Day at the Races","date":"14 Oct 2001","slug":"/2001-10-14-bad-day-at-the-races/","tags":"Y2001"},"html":"<p>It could have been a nice pleasant Indian Summer Sunday, but after Christopher decided that 5:30 was a fine time to wake up, and spending 3 hours hanging a single curtain rod for Mama's mother, the day was pretty much shot.</p>\n<p>Two comments from German newscasts this evening seem to have some truth. ZDF: As the Americans have no concrete plans for what happens after the Taleban, the attacks on Afghanistan are nothing but shots in in the air. ARD (on the anthrax scare): Fear is the main weapon of terrorists. Right now in the United States, that weapon is being used very effectively.</p>\n<p>And, almost as a footnote, the ARD newscast reported <a href=\"http://dailynews.yahoo.com/h/ap/20011014/wl/croatia_dialysis_deaths_5.html\">23 dialysis deaths in Croatia</a>, which, if I were a dialysis patient, would scare me a lot more than white powder in an envelope.</p>"},"next":{"fields":{"title":"Stofftier","date":"13 Oct 2001","slug":"/2001-10-13-stofftier/"}},"previous":{"fields":{"title":"Turbo Day","date":"15 Oct 2001","slug":"/2001-10-15-turbo-day/"}}}],"pathPrefix":"","first":false,"last":false,"index":968,"pageCount":1101}}

